@import url(https://fonts.googleapis.com/css2?family=Epilogue:wght@300;400&display=swap);
html,
body,
#root,
.app {
  box-sizing: border-box;
  background-color: #000;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  height: 100%;
  margin: 0;
  padding: 0;
  color: snow;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul,
ol,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
}

a,
button {
  transition: all 0.23s ease-in-out;
}

a {
  text-decoration: none;
  color: #000;
}

input {
  border-style: none;
}

button {
  cursor: pointer;
  border-style: none;
  outline: transparent;
  color: #2d2d2d;
}

.container {
  max-width: 1024px;
  margin: 0 auto;
  background-color: #2d2d2d;
  min-height: 100%;
  padding: 100px 0 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 0 0 4px deepskyblue;
}

@media (max-width: 600px) {
  .container {
    padding: 50px 0 10px;
    justify-content: center;
  }
}
@media (max-width: 414px) {
  .container {
    padding: 30px 0 0px;
    justify-content: center;
  }
}
@media (max-width: 320px) {
  .container {
    padding: 30px 0 0px;
    justify-content: flex-start;
  }
}
