@import url("https://fonts.googleapis.com/css2?family=Epilogue:wght@300;400&display=swap");
.button {
  font-family: "Epilogue", sans-serif;
  font-size: 1.5em;
  padding: 0.5em 1em;
  background-color: dodgerblue;
  border-radius: 30px;
  margin: 0 2px;
  &:hover {
    background-color: lightseagreen;
    color: snow;
  }
}
.disabled {
  opacity: 0.5;
  &:hover {
    background-color: dodgerblue;
    color: #2d2d2d;
  }
}

@media (max-width: 600px) {
  .button {
    font-size: 0.8em;
    padding: 2em;
    margin: 0 2px;
  }
}
